export * from '@microsoft/gulp-core-build';
export * from '@microsoft/gulp-core-build-typescript';

export * from './BuildRigConstants';
export * from './SPBuildRig';
export * from './BuildRig';

import * as gulp from 'gulp';
import { SPBuildRig } from './SPBuildRig';

export const rig: SPBuildRig = new SPBuildRig();

// Replace gcb.initialize to call our init stuff first.
export const initialize: (gulp: gulp.Gulp) => SPBuildRig = (gulp: gulp.Gulp): SPBuildRig => {
  rig.initialize(gulp);
  return rig;
};
